import React from 'react';
import PropTypes from 'prop-types';
import axios from 'axios'

class InsertMenu extends React.Component {
  insertMenu = ()=>{
    let menu = document.getElementById('menulist');
    let list = document.createElement('div');
    let key = document.createElement('input');
    //메뉴이름은 key 가격은 value로 id값 부여
    key.setAttribute('class','key')
    key.setAttribute('type',"text")
    key.setAttribute('value',"")
    let value = document.createElement('input');
    value.setAttribute('class','value')
    value.setAttribute('type',"text")
    value.setAttribute('value',"")
    list.appendChild(key);
    list.appendChild(value);
    menu.appendChild(list);
  }
  render() {
    if(!this.props._show) {
      return null;
    }
    const backdropStyle = {
      position: 'fixed',
      top: 0,
      bottom: 0,
      left: 0,
      right: 0,
      backgroundColor: 'rgba(0,0,0,0.3)',
      padding: 50
    };
    const modalStyle = {
      backgroundColor: '#fff',
      borderRadius: 5,
      maxWidth: 500,
      minHeight: 300,
      margin: '0 auto',
      padding: 30
    };
    return (
      <div className="backdrop" style={{backdropStyle}}>
        <div className="modal" style={{modalStyle}}>
        <button type="button" name="button" onClick={this.insertMenu}>메뉴추가</button>
        <p id="menulist">
        </p>
            <button onClick={this.props._onClose}>
              Close
            </button>
          </div>
        </div>
    );
  }
}

InsertMenu.propTypes = {
  _onClose: PropTypes.func.isRequired,
  _show: PropTypes.bool,
};

export default InsertMenu;
