import React from 'react';
import PropTypes from 'prop-types';
import axios from 'axios'
function updateStoreopeninghours(){
let openinghours = document.getElementById('openinghours').value;
let jsonFormat = {
  "openinghours" : openinghours
}
console.log(jsonFormat);
return jsonFormat;
}
class Updateopeninghours extends React.Component {
   updateStoreopeninghours = ()=>{
    axios.post('http://localhost:4000/updatestoreopeninghours', updateStoreopeninghours()
  ).then(res=>{
    console.log(res);
  })
  }
  render() {
    if(!this.props.show) {
      return null;
    }

    // The gray background
    const backdropStyle = {
      position: 'fixed',
      top: 0,
      bottom: 0,
      left: 0,
      right: 0,
      backgroundColor: 'rgba(0,0,0,0.3)',
      padding: 50
    };

    // The modal "window"
    const modalStyle = {
      backgroundColor: '#fff',
      borderRadius: 5,
      maxWidth: 500,
      minHeight: 300,
      margin: '0 auto',
      padding: 30
    };

    return (
      <div className="backdrop" style={{backdropStyle}}>
        <div className="modal" style={{modalStyle}}>
        <div>
        수정할 가게영업시간 : <input type="text" id="openinghours"></input>
        <button onClick={this.updateStoreopeninghours}>
          수정
        </button>
        </div>
        </div>
      </div>
    );
  }
}

Updateopeninghours.propTypes = {
  onClose: PropTypes.func.isRequired,
  show: PropTypes.bool,
};

export default Updateopeninghours;
