import React from 'react';
import PropTypes from 'prop-types';
import axios from 'axios'
function updateStoretel(){
let tel = document.getElementById('tel').value;
let jsonFormat = {
  "tel" : tel
}
console.log(jsonFormat);
return jsonFormat;
}
class Updatetel extends React.Component {
   updateStoretel = ()=>{
    axios.post('http://localhost:4000/updatestoretel', updateStoretel()
  ).then(res=>{
    console.log(res);
  })
  }
  render() {
    if(!this.props.show) {
      return null;
    }

    // The gray background
    const backdropStyle = {
      position: 'fixed',
      top: 0,
      bottom: 0,
      left: 0,
      right: 0,
      backgroundColor: 'rgba(0,0,0,0.3)',
      padding: 50
    };

    // The modal "window"
    const modalStyle = {
      backgroundColor: '#fff',
      borderRadius: 5,
      maxWidth: 500,
      minHeight: 300,
      margin: '0 auto',
      padding: 30
    };

    return (
      <div className="backdrop" style={{backdropStyle}}>
        <div className="modal" style={{modalStyle}}>
        <div>
        수정할 가게번호 : <input type="text" id="tel"></input>
        <button onClick={this.updateStoretel}>
          수정
        </button>
        </div>
        </div>
      </div>
    );
  }
}

Updatetel.propTypes = {
  onClose: PropTypes.func.isRequired,
  show: PropTypes.bool,
};

export default Updatetel;
