import React from 'react';
import PropTypes from 'prop-types';
import axios from 'axios'
import InsertMenu from './InsertMenu';
function parsingStoreInfo(){
  let storename = document.getElementById('storename').value;
  let id = document.getElementById('id').value;
  let tel = document.getElementById('tel').value;
  let location = document.getElementById('location').value;
  let openinghours = document.getElementById('openinghours').value;
  let description = document.getElementById('description').value;
  let menu = JSON.stringify(parsingMenuInfo()).toString();
  console.log(menu);
  let jsonFormat = {
    "storename" : storename ,
    "id" : id ,
    "tel" : tel ,
    "location" : location ,
    "menu" : menu ,
    "openinghours" : openinghours ,
    "description" : description
  }
  return jsonFormat;
}
function parsingMenuInfo(){
  let key = document.getElementsByClassName('key');
  let value = document.getElementsByClassName('value');
  let count = key.length;
  let array = []
  for (var i = 0; i < key.length; i++) {
    let jsonFormat = {}
    jsonFormat['name'] = key[i].value;
    jsonFormat['click'] = "0"
    jsonFormat['price'] = value[i].value
    array.push(jsonFormat)
  }
  return array;
}
class Modal extends React.Component {
  constructor(props){
      super(props);
      this.state={
          _isOpen: false
      }
  }
  _toggleModal = () => {
  this.setState({
    _isOpen: !this.state._isOpen
  });
  }
  inputStoreData = ()=>{
    axios.post('http://localhost:4000/storesetting',parsingStoreInfo()
  ).then(res=>{
    console.log(res);
  })
  }
  render() {
    // Render nothing if the "show" prop is false
    if(!this.props.show) {
      return null;
    }

    // The gray background
    const backdropStyle = {
      position: 'fixed',
      top: 0,
      bottom: 0,
      left: 0,
      right: 0,
      backgroundColor: 'rgba(0,0,0,0.3)',
      padding: 50
    };

    // The modal "window"
    const modalStyle = {
      backgroundColor: '#fff',
      borderRadius: 5,
      maxWidth: 500,
      minHeight: 300,
      margin: '0 auto',
      padding: 30
    };

    return (
      <div className="backdrop" style={{backdropStyle}}>
        <div className="modal" style={{modalStyle}}>
          <div>
              가게이름 : <input type="text" id="storename" ></input><br/>
              ID : <input type="text" id="id"></input><br/>
              전화번호 : <input type="text" id="tel"></input><br/>
              위치 : <input type="text" id="location"></input><br/>
              영업시간 : <input type="text" id="openinghours"></input><br/>
              설명 :<input type="text" id="description"></input><br/>
          </div>
          <div className="footer">
          <button onClick={this._toggleModal}>
            메뉴판만들기
          </button><br/>
          <InsertMenu _show={this.state._isOpen}
          _onClose={this._toggleModal}>
          </InsertMenu>
          <button onClick={this.inputStoreData}>
            가게추가
          </button>
            <button onClick={this.props.onClose}>
              Close
            </button>
          </div>
        </div>
      </div>
    );
  }
}

Modal.propTypes = {
  onClose: PropTypes.func.isRequired,
  show: PropTypes.bool,
};

export default Modal;
